import * as React from "react";
import { Link } from "react-router-dom";
import { accessPath, productsPath, rootPath } from "../routes";

/* tslint:disable */
export class SelfIntro extends React.Component {
  public componentDidMount() {
    window.scrollTo(0, 0);
  }

  public render() {
    return (
      <div>
        <div className="si-Content">
          <div className="si-Content_title"></div>
          <div className="si-Content_bar" />
          <div className="si-Content_description">
            <div className="si-Content_description-image" />
            <div className="si-Content_description-text">
               <br />
              1996413 <br />
              2015<br />
               <br />
              4 <br />
              20193 <br />
              Web2<br />
              Increments
            </div>
          </div>
        </div>
        <div className="si-Footer">
          <Link to={accessPath()} className="si-Footer_column"></Link>
          <Link to={rootPath()} className="si-Footer_column"></Link>
          <Link to={productsPath()} className="si-Footer_column"></Link>
        </div>
      </div>
    );
  }
}



